
.thumbnail {
  display: inline-block;
  margin: 2px;
  width: 50px;
  height: 50px;
  border-radius: 2px;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 1px rgba(255,255,255,0.2);
  cursor: pointer;
  overflow: hidden;
}
.thumbnail.active {
  box-shadow: inset 0 0 0 2px #fff;
}


