
.paginator {
  width: 100%;
  min-width: 300px;
  position: absolute;
  left: 0;
  bottom: 10px;
  text-align: center;
  margin: 0 auto;
}
.paginator .arrow {
  display: inline-block;
  position: absolute;
  vertical-align: top;
  height: 20px;
  width: 20px;
  margin: 18px 10px 0 10px;
  cursor: pointer;
}
.paginator .arrow.left {
  left: calc(50% - 170px);
}
.paginator .arrow.right {
  right: calc(50% - 170px);
}
.paginator .thumbnail-wrapper {
  display: inline-block;
}


