
.fancybox {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  height: calc(100% - 80px);
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
}
.fancybox .image-wrapper {
  display: inline-block;
  margin: 0 auto;
}
.fancybox .image-wrapper .header {
  height: 40px;
  position: relative;
}
.fancybox .image-wrapper .header .close {
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: 10px;
  cursor: pointer;
}
.fancybox .image-wrapper .image {
  display: block;
  max-height: calc(100vh - 160px);
  min-height: 200px;
  margin: 0 auto;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
@media screen and (min-width: 720px) {
.fancybox .image-wrapper .image {
    max-width: calc(85vw);
}
}
@media screen and (max-width: 720px) {
.fancybox .image-wrapper .image {
    max-width: calc(98vw);
}
}
.fancybox .image-wrapper .footer {
  position: relative;
  padding: 5px;
  text-align: left;
}
.fancybox .image-wrapper .footer span {
  display: inline-block;
  font-size: 14px;
  color: #fff;
}
.fancybox .image-wrapper .footer .count {
  position: absolute;
  font-size: 12px;
  right: 0;
}
.fancybox .arrow {
  display: inline-block;
  position: absolute;
  top: calc(50vh - 25px);
  cursor: pointer;
}
@media screen and (min-width: 720px) {
.fancybox .arrow {
    height: 40px;
    width: 40px;
}
}
@media screen and (max-width: 720px) {
.fancybox .arrow {
    height: 20px;
    width: 20px;
}
}
.fancybox .arrow.left {
  left: 0;
}
.fancybox .arrow.right {
  right: 0;
}


